.wemake {
    background: url('../../img/bg/wemake_bg.jpg') 50% 50% no-repeat;
    background-size: cover;
    position: relative;
}

.header {
    display: flex;
    align-items: center;
    padding-top: rem(10px);

    &.header_fixed{
        position: fixed;
        z-index: 4;
        width: 95%;
    }
}

.header__logo {
    margin-right: auto;
}


.nav-list{
    margin-right: rem(15px);

    @include tablets {
        display: none;
    }

    &.fixed-menu{
        position: fixed;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        //z-index: 4;

        .nav-list__item{
            display: flex;
            flex-direction: column;
            align-items: center;
            opacity: 0;
            animation: showMenu .3s;
            animation-fill-mode: forwards;

            &:after {
                margin: rem(20px) rem(12px);
            }
        }

        .nav-list__link{
            font-size: 18px;
        }
    }
}

.nav-list__item {
    display: inline-block;
    vertical-align: middle;
    white-space: nowrap;
    &:after {
        display: inline-block;
        vertical-align: middle;
        content: ' ';
        @include circle(rem(5px), $white);
        margin: 0 rem(12px);
    }
    &:last-child:after {
        display: none;
    }
}

.nav-list__link {
    color: $white;
    font-size: rem(13px);
    font-weight: 400;
    line-height: 1.42;
    text-transform: uppercase;
    display: inline-block;
    vertical-align: middle;
    transition: .5s;
    &:hover {
        text-decoration: underline;
    }
}

.header__btn{
    display: flex;
    align-items: center;
}

.wemake__content {
    width: 100%;
    display: flex;

    flex-grow: 1;
    align-items: center;
    padding-bottom: 10%;

    @include tablets {
        flex-wrap: wrap;
        justify-content: center;
    }
}

.wemake__img, .wemake__description {
    flex-basis: 50%;
    display: flex;

    @include tablets {
        flex-basis: 80%;
        text-align: center;
        justify-content: center;
    }
}

.wemake__img-pic{
    width: 100%;
    height: 100%;
    @include tablets {
        width: 70%;
        justify-content: center;
    }

}

.wemake__description {
    flex-direction: column;
    justify-content: center;

    @include tablets {
        flex-wrap: nowrap;
    }
}

.wemake__description-text {
    color: $white;
    @include lifehack();
    font-size: rem(65px);
    line-height: 1;
}

.nav-section-arrow{
    display: flex;
    justify-content: center;
}

.scroll-arrow{
    background: url('../../img/icons/arrow-scroll.svg') no-repeat;
    cursor: pointer;
    margin-bottom: rem(40px);
    width: 29px;
    height: 14px;
}